.browse-sub-menu{margin-top:40px;margin-bottom:40px;height:40px}.browse-sub-menu__wrap{border-bottom:1px solid #DEDEDE}.browse-sub-menu .tabs{width:100%;font-weight:600;font-size:22px;letter-spacing:0px;font-style:italic;position:relative;padding:0px;margin-bottom:4px}.browse-sub-menu .tabs li{display:inline;margin-right:30px}.browse-sub-menu .tabs a{color:#333;font-weight:normal}.browse-sub-menu .tabs a:hover{color:black;padding-bottom:3px;border-bottom:3px solid black}.browse-sub-menu .tabs a.active{font-weight:600;text-decoration:none;padding-bottom:3px;border-bottom:3px solid black;color:black}
