.sidebar{-webkit-transition:right .2s ease;-moz-transition:right .2s ease;-o-transition:right .2s ease;transition:right .2s ease;position:fixed;right:-310px;top:0px;width:300px;height:100%;background-color:#3F3F3F;z-index:1040;box-shadow:-2px 0px 6px rgba(0,0,0,0.35)}.sidebar--open{right:0}.sidebar__body{max-height:calc(100% - 60px);overflow-y:auto}.sidebar-header{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;height:60px;position:relative;border-bottom:1px solid rgba(38,38,38,0.5)}.cross{-webkit-transition:opacity .1s;-moz-transition:opacity .1s;-o-transition:opacity .1s;transition:opacity .1s;cursor:pointer;position:absolute;background-repeat:no-repeat;right:0px;top:0px;padding:22px;opacity:0.5}.cross:hover{opacity:1}.sidebar__frames-list{list-style:none;padding-left:0px;margin:0}.sidebar__row{font-size:14px;padding:10px;color:#fff;text-align:center;border-bottom:1px solid rgba(38,38,38,0.5)}.sidebar__row--title{font-weight:400;color:silver;text-align:center;padding-top:15px;padding-bottom:15px;text-transform:uppercase;letter-spacing:1px;border-bottom:none}.sidebar__row--frame{padding:15px 20px;text-align:left;cursor:pointer;height:60px}.sidebar__row--frame:hover,.sidebar_row--frame-active{background:#262626}.sidebar__row--logout{color:silver;border:0}.sidebar .anchor{font-weight:300;color:silver;text-decoration:none}.sidebar .anchor:hover{color:white}.frame-name{float:left;font-size:14px;font-weight:normal;letter-spacing:1px;text-transform:uppercase;color:white}.icon-settings{width:18px}.btn-frame-settings{float:right;padding-right:15px;opacity:0.2}.btn-frame-settings:hover{opacity:0.5}.frame-status{clear:both;font-size:13px;margin-right:20px;font-weight:normal;font-style:italic;letter-spacing:0.2px}.displaying{color:#B2B2B2}.not-displaying{color:#DDDDDD}.btn-logout{font-size:12px;font-weight:300;text-align:center;letter-spacing:0.5px;padding-top:36px}.btn-logout a{color:#B2B2B2;text-decoration:none}.btn-logout a:hover{color:black;text-decoration:none}.row-logout{position:fixed;bottom:0px;height:60px;text-align:center}.icon-settings{opacity:0.5}.icon-settings:hover{opacity:1}@media (min-width: 768px){.sidebar{right:-370px;width:360px}.sidebar--open{right:0}}@media (min-width: 992px){.sidebar{right:-450px;width:440px}.sidebar--open{right:0}}@media (min-width: 1200px){.sidebar--open{right:0}}
