.topbar{height:50px;background-color:#262626;color:#fff;position:fixed;top:0;left:0;width:100%;z-index:10;box-shadow:none;-webkit-transition:box-shadow 1s ease;-moz-transition:box-shadow 1s ease;-o-transition:box-shadow 1s ease;transition:box-shadow 1s ease}.topbar--shadow{box-shadow:0 0 8px #000}.topbar__tab{display:-webkit-inline-flex;display:inline-flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;-webkit-transition:all .1s;-moz-transition:all .1s;-o-transition:all .1s;transition:all .1s;font-size:11px;float:left;height:100%;padding:0 15px;color:#fff;background-color:transparent;text-transform:uppercase;cursor:pointer}.topbar__tab.active{background-color:#262626;color:#fff}.topbar__tab:hover{background-color:#262626;color:#fff}.topbar__tab:hover .sidebar-btn{opacity:1}.topbar__tab:focus{color:#fff}.topbar__tab--logo{display:none;padding:0 10px}.topbar__tab--logo img{padding:0;width:22px}.topbar__tab--menu{width:120px}.topbar__tab--link{text-transform:none}.topbar__tab--transparent:hover{background-color:transparent}.topbar__tab--selected-frame{text-transform:none;cursor:default;justify-content:flex-start;position:absolute;width:90%}.topbar__tab .sidebar-btn{-webkit-transition:opacity .1s;-moz-transition:opacity .1s;-o-transition:opacity .1s;transition:opacity .1s;border:1px solid #fff;height:18px;width:30px;border-radius:2px;opacity:0.5;background:url(data:image/svg+xml;base64,);background-position:center center;background-repeat:no-repeat}@media (min-width: 768px){.topbar{background-color:#3F3F3F;height:60px}.topbar__tab{padding:0 25px;letter-spacing:1.2px;font-size:13px}.topbar__tab--logo{padding:0 20px;display:inline-flex}.topbar__tab--logo img{padding:0;width:28px}.topbar__tab--selected-frame{float:right;position:relative;width:255px;padding:0}}@media (min-width: 992px){.topbar__tab--selected-frame{width:335px}}
