.profile-header{position:relative;padding:40px 15px 20px 15px;text-align:center;margin-bottom:10px;font-size:16px}.profile-header__name{font-size:36px;font-weight:600;margin-bottom:10px}.profile-header__social{color:silver;margin-bottom:5px}.profile-header__social a{color:silver}.profile-header__social a:hover{color:#3F3F3F}.profile-header__bullet{padding:0 10px;font-size:12px}.profile-header__bio{color:#3F3F3F}.profile-header__actions{position:absolute;top:10px;right:10px}@media (min-width: 768px){.profile-header{margin-bottom:40px}}
