.footerbar{display:-webkit-flex;display:flex;height:50px;width:100%;position:fixed;bottom:0px;left:0px;z-index:100;box-shadow:0px -2px 6px rgba(0,0,0,0.2)}.footerbar__tab{-webkit-flex:1;flex:1;position:relative;background:#3F3F3F;color:white;text-align:center}.footerbar__tab:hover{background:#333;color:#fff}.footerbar__tab--active{background:#262626}.footerbar__tab-icon-wrap{width:22px;height:22px;margin:auto;margin-top:7px;margin-bottom:6px;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.footerbar__tab-text{font-size:8px;font-weight:200;letter-spacing:1px;text-transform:uppercase}@media (min-width: 768px){.footerbar{display:none}}
