.collection-list-item{border:0.5px solid #DEDEDE}.collection-list-item__flex-wrap{display:-webkit-flex;display:flex}.collection-list-item__thumb{-webkit-flex-grow:0;flex-grow:0;width:80px;height:80px;position:relative;padding-bottom:10%;background-position:center center;background-size:cover}.collection-list-item__info{padding:15px;-webkit-flex-grow:4;flex-grow:4;-webkit-flex-basis:50%;flex-basis:50%}.collection-list-item__name{font-weight:400;font-size:14px}.collection-list-item__count{color:#aaa;font-weight:100;font-size:13px}.collection-list-item__open-btn{padding:0;-webkit-flex-grow:1;flex-grow:1;display:-webkit-flex;display:flex}.collection-list-item__open-btn img{margin:0;padding:0}@media (min-width: 768px){.collection-list-item{margin:0px;border-radius:4px}.collection-list-item__flex-wrap{-webkit-flex-direction:column;flex-direction:column}.collection-list-item__thumb{width:100%;padding-bottom:100%}}
