.notice-banner{width:100%;color:#fff;padding:15px}.notice-banner *{color:#fff}.notice-banner a{text-decoration:underline}.notice-banner a:hover{text-decoration:underline;color:#fff}.notice-banner--info{background-color:#7c7c7c}.notice-banner--success{background-color:#7c7c7c}.notice-banner--warning{background-color:#cb554d}@media (min-width: 768px){.notice-banner{max-width:700px;margin:20px auto;border-radius:3px;text-align:center}}
