.connected-indicator{position:relative;top:-1px;left:-5px;color:rgba(220,220,220,0.25);font-size:16px;padding:0 5px 0 0}.connected-indicator--connected{color:#37CFAD}.frame-item{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center}.frame-item__thumb{float:left;margin:10px 10px 10px 0;height:36px;width:36px;border-radius:2px;border:1px solid #fff;background-color:#000;background-position:center center;background-size:cover;position:relative}.frame-item__thumb--empty{border:1px solid silver;background-color:#262626}.frame-item__info{position:relative;top:-2px}.frame-item__name{font-size:14px;font-weight:400;letter-spacing:1px;text-transform:uppercase;margin-bottom:-3px}.frame-item__name:hover .frame-item__settings{display:inline}.frame-item__settings{height:16px;margin-left:10px;margin-top:-4px;display:none;cursor:pointer}.frame-item__status{font-size:11px;font-style:italic;color:#999;font-weight:300;letter-spacing:0px;line-height:1em;margin-top:2px}@media (min-width: 768px){.frame-item__thumb{float:left;height:40px;width:40px}.frame-item__status{font-size:14px;font-style:italic;color:#999;font-weight:300;margin-top:4px}}
