.like-button{-webkit-transition:all .07s ease-in-out;-moz-transition:all .07s ease-in-out;-o-transition:all .07s ease-in-out;transition:all .07s ease-in-out;-webkit-transform:scale(1, 1);-moz-transform:scale(1, 1);-ms-transform:scale(1, 1);-o-transform:scale(1, 1);transform:scale(1, 1);width:34px;height:34px;border-radius:34px;padding:6px 8px}.like-button:hover{background-color:#F6F6F6;-webkit-transform:scale(1.1, 1.1);-moz-transform:scale(1.1, 1.1);-ms-transform:scale(1.1, 1.1);-o-transform:scale(1.1, 1.1);transform:scale(1.1, 1.1)}.like-button--hover{cursor:pointer}
