.channel-list-item{border:0.5px solid #DEDEDE}.channel-list-item__flex-wrap{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center}.channel-list-item__thumb-wrap{flex-grow:0;width:20%;padding:5px}.channel-list-item__thumb{position:relative;border-radius:500px;width:100%;padding-bottom:100%;background-position:center center;-webkit-transform:scale(1, 1);-moz-transform:scale(1, 1);-ms-transform:scale(1, 1);-o-transform:scale(1, 1);transform:scale(1, 1);-webkit-transition:all .05s ease-in-out;-moz-transition:all .05s ease-in-out;-o-transition:all .05s ease-in-out;transition:all .05s ease-in-out}.channel-list-item__info{-webkit-flex-grow:3;flex-grow:3;width:60%;padding:10px}.channel-list-item__name{font-weight:400;font-size:14px}.channel-list-item__author{font-weight:100;font-size:14px}.channel-list-item__description,.channel-list-item__author{color:black;font-size:13px}.channel-list-item__actions{-webkit-flex-grow:1;flex-grow:1;display:-webkit-flex;display:flex;padding:0}@media (min-width: 768px){.channel-list-item{margin:0 0 30px 0;padding:10px;border-radius:4px}.channel-list-item__actions{display:none}.channel-list-item:hover .channel-list-item__thumb{-webkit-transform:scale(1.02, 1.02);-moz-transform:scale(1.02, 1.02);-ms-transform:scale(1.02, 1.02);-o-transform:scale(1.02, 1.02);transform:scale(1.02, 1.02)}.channel-list-item:hover .channel-list-item__push-btn{display:block}.channel-list-item:hover .channel-list-item__actions{display:block}.channel-list-item__flex-wrap{-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:flex-start;align-items:flex-start}.channel-list-item__thumb-wrap{width:100%}.channel-list-item__info{width:100%}}
